import type { NextFunction, Request, Response } from 'express';
import { Passport } from 'passport';

import {
    crowdCraftJwtStrategy,
    crowdcraftAnonymousStrategy,
} from '../strategy/index.js';

const passport = new Passport();

passport.use(crowdCraftJwtStrategy);
passport.use(crowdcraftAnonymousStrategy);

export const jwtAuthAnonymous = (
    req: Request,
    res: Response,
    next: NextFunction
): void => {
    passport.authenticate(['jwt', 'anon'], { session: false })(req, res, next);
};
